body {
  font-family: 'Roboto', 'Arial', sans-serif;
  color: #333333;
  font-size: 16px;
  line-height: 1.4em;
  -webkit-font-smoothing: antialiased;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.06);
}
a {
  text-decoration: none;
  color: #2466bc;
}
a:hover,
a:active,
a:focus {
  text-decoration: none;
  color: #0a366f;
  outline: none;
}
.padding-left {
  padding-left: 60px;
}
.white {
  color: #fff;
}
.h2 {
  font-size: 28px;
  font-weight: normal;
}
.h3 {
  font-size: 21px;
}
.header {
  border-bottom: 2px solid #2466bc;
}
.header__logo {
  display: block;
  margin: 15px 0;
}
.header__logo img {
  height: 40px;
}
.main-nav {
  width: auto;
  overflow: hidden;
}
.main-nav__list {
  padding: 0;
  margin: 0;
}
.main-nav__item {
  display: inline-block;
  vertical-align: top;
  position: relative;
  padding: 0;
  margin: 0 12px 0 0;
}
.main-nav__item:after {
  content: '';
  display: block;
  position: absolute;
  bottom: -5px;
  left: 0;
  height: 4px;
  width: 100%;
  background-color: #ea544e;
  transition: bottom 0.4s;
}
.main-nav__item:hover:after {
  bottom: 0;
  transition: bottom 0.15s;
}
.main-nav__item > a {
  display: inline-block;
  vertical-align: top;
  color: #000;
  padding: 5px 15px;
  line-height: 60px;
}
.main-nav__item > a:hover,
.main-nav__item > a:focus {
  text-decoration: none;
  color: #2466bc;
}
.main-nav__item--favs a {
  padding-left: 37px;
}
.main-nav__item--favs a:before,
.main-nav__item--favs a:after {
  content: '';
  position: absolute;
  display: block;
  width: 17px;
  height: 17px;
  left: 12px;
  background-image: url(../img/supheader__icons.png);
  background-size: cover;
}
.main-nav__item--favs a:before {
  top: 26px;
  opacity: 1;
  background-position: -36px 0;
}
.main-nav__item--favs a:after {
  top: 40px;
  background-position: -53px 0;
  opacity: 0;
}
.main-nav__item--favs a:hover:before {
  opacity: 0;
  transition: opacity 0.5s;
}
.main-nav__item--favs a:hover:after {
  top: 26px;
  opacity: 1;
  transition: top 0.4s;
  transition: opacity 0.4s;
}
.main-nav__btn {
  display: inline-block;
  vertical-align: top;
  position: relative;
  z-index: 1;
  background-color: #ea544e;
  line-height: 60px;
  padding: 5px 25px;
  color: #fff;
  transition: all 0.5s;
}
.main-nav__btn:hover,
.main-nav__btn:focus {
  text-decoration: none;
  color: #fff;
}
.main-nav__btn:hover {
  background-color: #d44c46;
  transition: all 0.15s;
}
.main-nav__btn:active {
  transform: scale(0.95, 0.95);
  transition: all 0.15s;
}
.supheader {
  width: auto;
  background-color: #f2f2f2;
  font-size: 12px;
  color: #666666;
}
.supheader__list {
  margin: 0;
  padding-left: 15px;
  padding-right: 15px;
}
.supheader__item {
  display: inline-block;
  vertical-align: top;
  position: relative;
  padding: 3px 20px 4px 0;
  margin: 0 25px 0 0;
}
.supheader__item:last-child {
  margin-right: 0;
}
.supheader__item:after {
  position: absolute;
  content: '';
  top: 40%;
  right: 6px;
  width: 0;
  height: 0;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  border-top: 4px solid;
}
.supheader__item:hover {
  cursor: pointer;
  color: #2466bc;
}
.supheader__item--curr {
  padding-left: 24px;
}
.supheader__item--curr:before {
  content: '';
  position: absolute;
  display: block;
  width: 14px;
  height: 15px;
  left: 0;
  top: 7px;
  background-image: url(../img/supheader__sprite.png);
  background-position: -42px -12px;
  background-size: auto 27px;
}
.supheader__item--lang {
  padding-left: 24px;
}
.supheader__item--lang:before {
  content: '';
  position: absolute;
  display: block;
  width: 18px;
  height: 12px;
  left: 0;
  top: 8px;
  background-image: url(../img/supheader__sprite.png);
  background-position: 18px 0;
  background-size: auto 27px;
