.btn--white {
  color: #fff;
  border: 2px solid #fff;
}
.btn--white:hover {
  color: #fff;
  background-color: #0a366f;
  transition: .15s;
}
.btn--white:active {
  transform: scale(0.95, 0.95);
  transition: 0.2s;
}
.dropdown {
  position: absolute;
  top: 25px;
  right: 0;
  z-index: 5;
  background-color: #f2f2f2;
  border-radius: 2px;
  border: 1px solid #d2d2d2;
  box-shadow: 0 1px 10px #bbb;
  padding: 30px 15px 5px;
  color: #333333;
  visibility: hidden;
  transform-origin: 100% 0;
  transform: scaleY(0.9);
  transition: all 0.3s;
  opacity: 0;
}
.dropdown:before {
  content: '';
  position: absolute;
  top: -5px;
  right: 22px;
  width: 9px;
  height: 9px;
  background-color: #f2f2f2;
  display: block;
  transform: scaleX(1.4) rotate(45deg);
  border-top: 1px solid #d2d2d2;
  border-left: 1px solid #d2d2d2;
  border-radius: 2px 0 0 0;
}
.dropdown__title {
  font-size: 21px;
}
.dropdown__list {
  padding: 0 0 0 24px;
  list-style: none;
  font-size: 14px;
  color: #2466bc;
}
.dropdown__list-item {
  position: relative;
  padding: 5px 0;
  margin: 10px 0;
  opacity: 0;
  top: -15px;
}
.dropdown__list-item:after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: none;
  background-color: #c0d8fa;
}
.dropdown__list-item:hover:after {
  display: block;
}
.dropdown--lang .dropdown__list {
  padding: 0 30px 0 24px;
}
.dropdown--lang .dropdown__list-item:after {
  width: 168px;
  left: -40px;
}
.dropdown--lang .dropdown__list-item:before {
  content: '';
  position: absolute;
  display: block;
  z-index: 10;
  width: 18px;
  height: 12px;
  top: 10px;
  left: -23px;
  background-image: url(../img/supheader__sprite.png);
  background-size: auto 27px;
}
.dropdown--lang .dropdown__list-item--lv:before {
  background-position: -18px 0;
}
.dropdown--lang .dropdown__list-item--lt:before {
  background-position: -36px 0;
}
.dropdown--lang .dropdown__list-item--pl:before {
  background-position: 36px 0;
}
.dropdown--lang .dropdown__list-item--ru:before {
  background-position: 18px 0;
}
.dropdown--curr .dropdown__list-item {
  width: 150px;
}
.dropdown--curr .dropdown__list-item:after {
  width: 206px;
  left: -40px;
}
.dropdown--curr .dropdown__list-item:before {
  content: '';
  position: absolute;
  display: block;
  z-index: 10;
  width: 14px;
  height: 15px;
  top: 8px;
  left: -23px;
  background-image: url(../img/supheader__sprite.png);
  background-size: auto 27px;
}
.dropdown--curr .dropdown__list-item--euro:before {
  background-position: 0 -12px;
}
.dropdown--curr .dropdown__list-item--dollar:before {
  background-position: -14px -12px;
}
.dropdown--curr .dropdown__list-item--zloty:before {
  background-position: -28px -12px;
}
.dropdown--curr .dropdown__list-item--rubl:before {
  background-position: -42px -12px;
}
.dropdown__list-item span {
  margin-right: 10px;
  color: #b2b2b2;
}
.supheader__item--lang:hover .dropdown {
  visibility: visible;
  transform: scaleY(1);
  opacity: 1;
  transition: all 0.2s;
}
.supheader__item--lang:hover .dropdown__list-item {
  opacity: 1;
  top: 0;
  transition: opacity 0.3s, top 0.5s;
}
.supheader__item--curr:hover .dropdown {
  visibility: visible;
  transform: scaleY(1);
  opacity: 1;
  transition: all 0.2s;
}
.supheader__item--curr:hover .dropdown__list-item {
  opacity: 1;
  top: 0;
  transition: opacity 0.3s, top 0.5s;
}
.category-banner {
  width: 100%;
  height: 300px;
  background-image: url(../img/category-banner.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}
.category-banner:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: block;
  visibility: hidden;
  background-color: rgba(255, 255, 255, 0);
  border: 0 solid #0a366f;
  transition: all 0.1s;
}
.category-banner__wrapper {
  background-color: #2466bc;
  position: absolute;
  right: 0;
  z-index: 5;
  top: 25px;
  margin-right: -15px;
  width: 320px;
  padding: 20px 20px 30px;
  color: #fff;
  transition: all 0.4s;
}
.category-banner__wrapper:after {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 10px;
  bottom: -10px;
  left: 0;
  background-color: rgba(255, 255, 255, 0.6);
}
.category-banner:hover:after {
  visibility: visible;
  cursor: pointer;
  border: 3px solid #0a366f;
  background-color: rgba(255, 255, 255, 0.2);
  transition: all 0.1s;
}
.category-banner:hover .category-banner__wrapper {
  cursor: pointer;
  background-color: #0a366f;
  transition: all 0.15s;
}
.category-banner__title {
  margin: 0;
  margin-bottom: 5px;
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 42px;
  line-height: 1.2em;
}
.category-banner__adds {
  font-size: 21px;
  margin: 0;
}
.category-banner__flow-inner {
  position: absolute;
  bottom: 0;
  height: 70px;
  width: 100%;
  padding: 12px 20px 30px;
  background-color: rgba(255, 255, 255, 0.85);
}
.category-banner__flow {
  margin: 0;
}
.product-banner {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  padding: 7px;
  position: relative;
  z-index: 0;
}
.product-banner:nth-child(2n) {
  background-color: #f2f2f2;
}
.product-banner:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: block;
  visibility: hidden;
  border: 2px solid transparent;
  transition: border 0.4s;
}
.product-banner__img-inner {
  width: 135px;
  height: 135px;
  overflow: hidden;
  position: relative;
}
.product-banner__img {
  display: block;
  position: absolute;
  max-height: 100%;
  width: auto;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}
.product-banner__wrapper {
  margin-left: 15px;
}
.product-banner__title {
  margin: 5px 0;
  font-size: 18px;
  font-weight: normal;
  color: #2466bc;
  cursor: pointer;
  position: relative;
  z-index: 3;
}
.product-banner__title:hover a,
.product-banner__title:focus a {
  text-decoration: none;
}
.product-banner__title:hover a {
  color: #0a366f;
  cursor: pointer;
}
.product-banner:hover:after {
  visibility: visible;
  cursor: pointer;
  border: 2px solid #0a366f;
  transition: border 0.1s;
}
.product-banner:hover .product-banner__title a {
  color: #0a366f;
}
.product-banner__flow p {
  margin: 0;
  color: #666666;
  line-height: 1.5em;
}
.product-banner__rating {
  position: absolute;
  z-index: 1;
  bottom: 15px;
  display: block;
  background-color: #333;
  height: 17px;
  width: 113px;
  max-width: 112px;
}
.product-banner__rating:before {
  position: absolute;
  content: '';
  z-index: -1;
  width: 70%;
  height: 100%;
  display: block;
  background-color: #fbb03b;
  left: 0;
  top: 0;
}
.product-banner__rating:after {
  position: absolute;
  content: '';
  bottom: -1px;
  width: 115px;
  height: 19px;
  background-image: url(../img/rating_star.png);
  background-repeat: repeat-x;
  background-size: 23px auto;
  background-position: 0 0;
}
.product-banner:nth-child(2n) .product-banner__rating:after {
  background-position: 0 -19px;
}
.product-banner:nth-child(2n) .product-banner__rating:before {
  width: 92%;
}
.product-banner:nth-child(3n) .product-banner__rating:before {
  width: 60%;
}
.product-banner__fav {
  box-sizing: content-box;
  position: absolute;
  bottom: 12px;
  z-index: 3;
  right: 15px;
  height: 20px;
  width: 24px;
  opacity: .5;
  background-image: url(../img/fav_icon.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  transition: opacity 0.4s;
}
.product-banner__fav:hover {
  opacity: 1;
  cursor: pointer;
  transition: opacity 0.2s;
}
.product-banner__fav:active,
.product-banner__fav:visited,
.product-banner__fav:focus {
  background-position: -24px 0;
  opacity: 1;
}
.somit--blue {
  background-color: #2466bc;
  margin-top: 50px;
  padding: 30px 0 50px;
}
.somit--blue .btn {
  margin: 50px auto 20px;
  display: block;
  width: 318px;
}
.categories {
  display: flex;
  flex-flow: row wrap;
  margin: 0 -3px;
}
.categories__item-inner {
  width: 25%;
  padding: 3px;
  position: relative;
}
.categories__item-inner:before {
  display: block;
  content: '';
  padding-top: 100%;
}
.categories__item {
  position: absolute;
  margin: 3px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #5085c9;
  color: #fff;
}
.categories__item:before {
  content: '';
  position: absolute;
  width: 90px;
  height: 75px;
  top: 28%;
  left: 0;
  background-image: url(../img/category-arrow.png);
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
}
.categories__item:hover {
  background-color: #0a366f;
  transition: background-color 0.3s;
}
.categories__item:hover:before {
  opacity: 1;
  left: 37%;
  transition: opacity 0.3s 0.05s, left ease-out 0.4s;
}
.categories__item:hover .categories__item-name:before {
  opacity: 0;
  transition: opacity 0.2s;
}
.categories__item-name {
  position: relative;
  padding-top: 65%;
  font-size: 32px;
  text-transform: uppercase;
  text-align: center;
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
}
.categories__item-name:before {
  content: '';
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  width: 189px;
  height: 139px;
  background-image: url(../img/category-icon.png);
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 1;
}
